.sa-hero {
    // background-color: green;
    max-width: 1600px;
    margin: 0 auto 0 auto;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.sa-headings-container {
    display: flex;
    justify-content: center;
    font-family: "JetBrains Mono";
    font-size: 18px;
    letter-spacing: 2px;
    width: 100%;
    color: #c3c3c3;
    @media(min-width: 768px) {
        width: 95%;
        justify-content: space-between;
        margin: 0 auto 0 auto;
        font-size: 30px;
    }
    @media(min-width:1024px) {
        font-size: 40px;
    }
    @media(min-width: 1440px) {
        font-size: 55px;
    }
    @media(min-width: 1600px) {
        width: 100%;
        font-size: 65px;
    }
}

.sa-headings-container h6 {
    display: none;
    @media(min-width: 768px){
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

