.app {
  background-color: black;
  width: 100%;
}

.nav {
  max-width: 1600px;
  margin: 5px auto 0 auto;
  display: flex;
  flex-direction: column;
  width: 95%;
  font-family: "JetBrains Mono";
  font-weight: bold;
  letter-spacing: 4px;
}
@media (min-width: 1600px) {
  .nav {
    width: 100%;
  }
}

.logo-links-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: white;
}

.links-container {
  display: none;
  width: 387px;
  justify-content: space-between;
  color: white;
  font-size: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-right: 0px;
}
@media (min-width: 425px) {
  .links-container {
    width: 357px;
  }
}
@media (min-width: 768px) {
  .links-container {
    display: flex;
    font-weight: 100;
  }
}
.shopall {
  text-decoration: none;
  color: white;
  cursor: pointer;
  letter-spacing: 2px;
}

.about {
  cursor: pointer;
  letter-spacing: 2px;
}

.cart {
  cursor: pointer;
  letter-spacing: 2px;
}

.logo {
  display: flex;
  align-items: center;
  margin-left: 3%;
  font-size: 25px;
  text-decoration: none;
  color: white;
  cursor: pointer;
}

.divider {
  width: 100%;
  height: 2px;
  background-color: white;
  margin-top: 4px;
}

.nav-hamburger {
  width: 25px;
  height: 18px;
  margin-top: 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .nav-hamburger {
    display: none;
  }
}

.nav-hamburger-top {
  width: 100%;
  height: 2px;
  background-color: white;
}

.nav-hamburger-middle {
  width: 100%;
  height: 2px;
  background-color: white;
}

.nav-hamburger-bottom {
  width: 100%;
  height: 2px;
  background-color: white;
}

#hamburgerId {
  position: absolute;
  z-index: 2;
  /* width: 500px; */
  width: 100%;
  height: 100vh;
  background-color: black;
  /* display: none; */
  visibility: hidden;
  right: -768px;
  transition-duration: 1s;
  top: 0px;
  transition: visibility 0.3s, right 0.3s ease-in-out;
}
@media (min-width: 769px) {
  #hamburgerId {
    right: -500px;
    width: 500px;
  }
}

#hamburgerId.open {
  /* display: flex; */
  /* transition-duration: 2s; */
  visibility: visible;
  /* width: 500px; */
  width: 100%;
  height: 100vh;
  top: 0px;
  right: 0px;
  border-left: 3px solid #c3c3c3;
}
@media (min-width: 769px) {
  #hamburgerId.open {
    width: 500px;
  }
}

.hamburger-exit {
  height: 33px;
  width: 30px;
  background-color: black;
  color: #c3c3c3;
  border: none;
  font-size: 20px;
  cursor: pointer;
  float: right;
  margin-right: 11px;
  margin-top: 7px;
  margin-bottom: 1px;
}

.hamburger-button-container {
  display: flex;
  justify-content: right;
  margin-right: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
}/*# sourceMappingURL=nav.css.map */